import { CopyIcon } from "assets/icons/copy";
import { ExploreIcon } from "assets/icons/explore";
import { DisconnectIcon } from "assets/icons/disconnect";
import {
  Button,
  EnhancerProps,
  Heading,
  Pane,
  Popover,
  ChevronDownIcon,
  UserIcon,
  SearchInput,
  MoreIcon,
  TextInputOwnProps,
  majorScale,
  IconButton,
  Tooltip,
  SearchIcon,
} from "evergreen-ui";
import { Col, Row } from "react-flexbox-grid";
import { useMediaQuery } from "react-responsive";
import { config } from "utils/config";
import { addressShortener } from "utils/token";
import { Divider } from "./Divider";
import { LightIcon } from "assets/icons/light";
import { SplitButton } from "./SplitButton";

function DisconnectButton(props: EnhancerProps): JSX.Element {
  return (
    <Tooltip content="disconnect">
      <IconButton
        {...props}
        className="disable-hover-border"
        backgroundColor={config.secondaryColor}
        icon={<DisconnectIcon />}
      />
    </Tooltip>
  );
}

function ExploreButton(props: EnhancerProps): JSX.Element {
  return (
    <Tooltip content="explore">
      <IconButton
        {...props}
        className="disable-hover-border"
        backgroundColor={config.secondaryColor}
        icon={<ExploreIcon />}
      />
    </Tooltip>
  );
}

function CopyButton(props: EnhancerProps): JSX.Element {
  return (
    <Tooltip content={"copy"}>
      <IconButton
        {...props}
        className="disable-hover-border"
        backgroundColor={config.secondaryColor}
        icon={<CopyIcon />}
      />
    </Tooltip>
  );
}

function ThemeButton(props: EnhancerProps): JSX.Element {
  return (
    <SplitButton
      {...props}
      iconBefore={<p>Light Theme</p>}
      iconAfter={<LightIcon />}
    />
  );
}

function LanguageButton(props: EnhancerProps): JSX.Element {
  return (
    <SplitButton
      {...props}
      iconBefore={<p>Language</p>}
      iconAfter={<p>EN</p>}
    />
  );
}

function AccountButton(props: EnhancerProps): JSX.Element {
  return (
    <Popover
      bringFocusInside
      content={
        <Pane paddingX={majorScale(4)} paddingY={majorScale(2)} width={320}>
          <Row middle="xs">
            <Col xs>
              <Heading size={600}>
                {addressShortener("0xf1D4D16aAc4E116120312BA0D5670cF0662650AD")}
              </Heading>
            </Col>
            <Col>
              <Row end="xs">
                <CopyButton marginRight={majorScale(1)} />
                <ExploreButton marginRight={majorScale(1)} />
                <DisconnectButton marginRight={majorScale(2)} />
              </Row>
            </Col>
          </Row>
          <Divider />
          <LanguageButton />
          <ThemeButton />
        </Pane>
      }
    >
      <Button
        {...props}
        borderRadius={20}
        iconBefore={<UserIcon />}
        iconAfter={<ChevronDownIcon />}
        fontWeight={`bolder`}
      >
        {addressShortener("0xf1D4D16aAc4E116120312BA0D5670cF0662650AD")}
      </Button>
    </Popover>
  );
}

function ConnectWalletButton(props: EnhancerProps): JSX.Element {
  return (
    <Button {...props} appearance="primary" fontWeight={`bolder`} fontSize={16}>
      Connect Wallet
    </Button>
  );
}

function ConnectButton(props: EnhancerProps): JSX.Element {
  return (
    <Popover
      bringFocusInside
      content={
        <Pane paddingX={majorScale(5)} paddingY={majorScale(2)} width={320}>
          <ConnectWalletButton width={`100%`} paddingY={majorScale(3)} />
          <Divider />
          <LanguageButton />
          <ThemeButton />
        </Pane>
      }
    >
      <Button
        {...props}
        borderRadius={20}
        iconAfter={<ChevronDownIcon />}
        fontWeight={`bolder`}
        appearance="primary"
      >
        Connect
      </Button>
    </Popover>
  );
}

function WalletButton(props: EnhancerProps): JSX.Element {
  const connected = false;

  if (connected) return <AccountButton {...props} />;
  return <ConnectButton {...props} />;
}

function MoreButton(props: EnhancerProps): JSX.Element {
  return (
    <IconButton
      {...props}
      className="disable-hover-border"
      background="transparent"
      icon={<MoreIcon />}
    />
  );
}

function SearchButton(props: EnhancerProps): JSX.Element {
  return (
    <IconButton
      {...props}
      className="disable-hover-border"
      background="transparent"
      icon={<SearchIcon />}
    />
  );
}

function SearchBar(props: TextInputOwnProps): JSX.Element {
  return (
    <SearchInput
      {...props}
      className="disable-hover-border"
      placeholder="search token or issuer address"
    />
  );
}

function PortraitNavigation(): JSX.Element {
  return (
    <Row middle="xs">
      <Col xs>
        <Heading size={600}>Nav</Heading>
      </Col>

      <Col>
        <Row end="xs">
          <Col xs>
            <SearchButton marginRight={10} />
            <MoreButton marginRight={10} />
            <WalletButton />
          </Col>
        </Row>
      </Col>
    </Row>
  );
}

function LandscapeNavigation(): JSX.Element {
  return (
    <Row middle="xs">
      <Col xs>
        <Heading size={600}>PortraitNavigation</Heading>
      </Col>

      <Col xs>
        <SearchBar width={`100%`} />
      </Col>

      <Col xs>
        <Row end="xs">
          <Col xs>
            <MoreButton marginRight={10} />
            <WalletButton />
          </Col>
        </Row>
      </Col>
    </Row>
  );
}

export function Navigation(): JSX.Element {
  const isPortrait = useMediaQuery({ query: "(orientation: portrait)" });

  return (
    <Pane
      paddingX={16}
      paddingY={10}
      background={config.secondaryColor}
      position="sticky"
    >
      {isPortrait ? <PortraitNavigation /> : <LandscapeNavigation />}
    </Pane>
  );
}
